const ValidasiPindahBarang = (value) => {
    const errors = {};
    if (!value.kode_gudang) {
        errors.kode_gudang = "Kode Gudang Tidak Boleh Kosong";
    }
    if (!value.kode_baki) {
        errors.kode_baki = "Nama Baki Tidak Boleh Kosong";
    }

    if (!value.statusAktif) {
        errors.statusAktif = "Status Tidak Boleh Kosong";
    }

    return errors;
}
export default ValidasiPindahBarang;


