import React, { Component } from "react";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { setBarangPindah } from "../../../actions/databarang_action";
import { getDataBaki, getDataGudang } from "../../../actions/datamaster_action";
import {
  ReanderSelect,
  SkeletonLoading,
  ReanderField,
} from "../../../components/helpers/field";
import { ToastNotification } from "../../../components/helpers/notification";
import Tabel from "../../../components/helpers/tabel";
import { getData } from "../../../config/axios";
import ValidasiPindahBarang from "../../../Validasi/Barang/PindahBarang/ValidasiPindahBarang";
import Loading from 'react-fullscreen-loading';
const maptostate = (state) => {
  return {
    datagudang: state.datamaster.getDataGudang,
    databaki: state.datamaster.getDataBaki,
    setBarangPindah: state.databarang.setBarangPindah,
  };
};
class HeadPindahBarang extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      isLoadingBatal:false,
      isLoadingBaki: false,
    };
  }
  componentWillUnmount() {}
  componentDidMount() {
    this.props.dispatch(getDataGudang());
    this.props.dispatch(getDataBaki());
    setTimeout(() => {
      // document.getElementById('kode_gudang').p
      this.setState({ kode_gudang: 'TOKO' });
      this.props.change('kode_gudang','TOKO')
    }, 100);
  }
  getKodeBarcode(e) {
    clearTimeout(this.delay)
    this.delay = setTimeout(() => {
      getData("barang/open/kode-barcode?kode_barcode=" + e)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Dicari Tidak Ada");
          // return false
        } else {
          let data = JSON.parse(localStorage.getItem("data_barang_tmp")) || [];
          let index = data.findIndex((hasil) => hasil.kode_barcode === e);
          if (index === -1) {
            data.push(res.data[0]);
            localStorage.setItem("data_barang_tmp", JSON.stringify(data));
            this.props.dispatch(setBarangPindah(data));
            this.props.change("kode_barcode", "");
          }
        }
      })
      .catch((err) => {
        ToastNotification("info", "Data Yang Dicari Tidak Ada");
      });
    }, 1500);
  }
  batalHancur() {
    this.setState({ isLoadingBatal: true })
    setTimeout(() => {
      this.props.dispatch(setBarangPindah([]));
      localStorage.setItem('data_barang_tmp', JSON.stringify([]))
      this.setState({ isLoadingBatal: false })
    }, 500);
  }
  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off"
      onKeyPress={(e) => {
        e.key === "Enter" && e.preventDefault();
      }}
      >
        <div className="row">
          <div className="col-lg-4">
            <Field
              id="kode_gudang"
              label="Kode Gudang"
              name="kode_gudang"
              onChange={(e) => {
                this.setState({ kode_gudang: e });
                this.props.change("kode_baki", "");
              }}
              options={this.props.datagudang
                .filter((list) => list.kode_gudang !== undefined)
                .map((list) => {
                  let data = {
                    value: list.kode_gudang,
                    name: list.kode_gudang + " - " + list.nama_gudang,
                  };
                  return data;
                })}
              placeholder={"Silahkan Pilih Kode Gudang"}
              component={ReanderSelect}
            />
          </div>
          <div className="col-lg-4">
            {this.state.isLoadingBaki === false ? (
              <Field
                label="Baki Tujuan"
                name="kode_baki"
                options={this.props.databaki
                  .filter((list) => list.kode_gudang === this.state.kode_gudang)
                  .map((databaki) => {
                    let data = {
                      value: databaki.kode_baki,
                      name: databaki.kode_baki + " - " + databaki.nama_baki,
                    };
                    return data || [];
                  })}
                placeholder={"Silahkan Pilih Baki Tujuan"}
                component={ReanderSelect}
              />
            ) : (
              <SkeletonLoading label="Baki Tujuan" />
            )}
          </div>

          <div className="col-lg-4">
            <Field
              onChange={(e) => this.getKodeBarcode(e.target.value)}
              name="kode_barcode"
              component={ReanderField}
              type="text"
              label="Kode Barcode"
              placeholder="Masukan Kode Barcode"
            />
          </div>
        </div>
        <Tabel
          keyField="kode_barcode"
          exportCsv={false}
          pagination="off"
          data={
            this.props.setBarangPindah.length === 0
              ? JSON.parse(localStorage.getItem("data_barang_tmp"))
              : this.props.setBarangPindah
          }
          columns={this.props.columns}
          empty={true}
          textEmpty="Data Barang Kosong"
        />
        <div className="row">
          <div className="col-10">
            <button
              className="btn btn-primary"
              disabled={this.props.submitting}
              type="submit"
            >
              {this.props.isLoading ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Menyimpan
                </>
              ) : (
                "Simpan Data"
              )}
            </button>
            &nbsp;
            <button
              className="btn btn-warning"
              type="button"
              onClick={()=>this.batalHancur()}
              disabled={this.props.isLoadingBatal}
            >
              {this.state.isLoadingBatal ? (
                <>
                  <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                  Membatal
                </>
              ) : (
                "Batal"
              )}
            </button>
          </div>
          {/* <div className="2">
            {JSON.parse(localStorage.getItem("data_barang_tmp")) ===
            null ? null : (
              <>
                Total Barang :{" "}
                {this.props.setBarangPindah.length === 0
                  ? JSON.parse(localStorage.getItem("data_barang_tmp"))
                      .length || 0
                  : this.props.setBarangPindah.length || 0}
                <br />
                Total Berat :
                {this.props.setBarangPindah.length === 0
                  ? JSON.parse(localStorage.getItem("data_barang_tmp"))
                      .map((column, key) => column.berat)
                      .reduce((a, b) => a + b, 0)
                      .toFixed(1)
                  : this.props.setBarangPindah
                      .map((column, key) => column.berat)
                      .reduce((a, b) => a + b, 0)
                      .toFixed(1)}
              </>
            )}
          </div> */}
        </div>
        {this.props.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null}
      </form>
    );
  }
}

HeadPindahBarang = reduxForm({
  form: "HeadPindahBarang",
  enableReinitialize: true,
  validate: ValidasiPindahBarang,
})(HeadPindahBarang);
export default connect(maptostate, null)(HeadPindahBarang);
