import React from "react";
import { Link } from "react-router-dom";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { connect } from "react-redux";
import HeadPindahBarang from "./HeadPindahBarang.jsx";
import Swal from "sweetalert2";
import { setBarangPindah } from "../../../actions/databarang_action.jsx";
import { postData } from "../../../config/axios.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
const maptostate = (state) => {
  return {
    setBarangPindah: state.databarang.setBarangPindah,
  };
};

class FormPindahBarang extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isEdit: false,
      modalDialog: false,
      isLoading: false,
      columns: [
        {
          dataField: "kode_barcode",
          text: "Kode Barcode",
          footerAttrs: {
            colSpan: "5",
          },
          footer: (cell, row, num, index) => {
            return (
              <div>Total Barang : {this.props.setBarangPindah.length === 0
                ?  JSON.parse(localStorage.getItem("data_barang_tmp")) === null  ? 0 :  JSON.parse(localStorage.getItem("data_barang_tmp"))
                    .length || 0
                : this.props.setBarangPindah.length || 0} </div>
            );
          },
        },
        {
          dataField: "kode_group",
          text: "Gudang Asal",
        },
        {
          dataField: "kode_toko",
          text: "Baki Asal",
        },
        {
          dataField: "nama_barang",
          text: "Nama Barang",
        },
        {
          dataField: "nama_atribut",
          text: "Nama Atribut",
        },
        {
          dataField: "berat",
          text: "Berat",
          formatter: (cell) => {
            return cell.toFixed(3);
          },
          footer: (columnData) =>
            columnData.reduce((acc, item) => acc + item, 0).toFixed(3),
        },

        {
          dataField: "action",
          text: "Action",
          csvExport: false,
          headerClasses: "text-center",
          formatter: (rowcontent, row,index) => {
            return (
              <div className="row text-center">
                <div className="col-12">
                  <button type="button" onClick={()=>this.hapusdata(index)} className="btn btn-danger mr-3">
                    <i className="fa fa-trash"></i>
                  </button>
                </div>
              </div>
            );
          },
          footer : ""
        },
      ],
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  hapusdata(id) {
    Swal.fire({
      title: "Anda Yakin !!",
      text: "Ingin Menghapus Data Ini ?",
      icon: "warning",
      position: "top-center",
      cancelButtonText: "Tidak",
      showCancelButton: true,
      confirmButtonText: "OK",
      showConfirmButton: true,
    }).then((result) => {
      if (result.isConfirmed) {
        let data = JSON.parse(localStorage.getItem('data_barang_tmp'));
        data.splice(id, 1)
        localStorage.setItem('data_barang_tmp', JSON.stringify(data));
        this.props.dispatch(setBarangPindah(data));
        // deleteLocalItemBarcode("data_barang_tmp", id);
        // this.props.dispatch(getListReturnSupplier());
      }
    });
  }
  componentDidMount() {
    this.props.dispatch(setBarangPindah([]));
    // this.props.dispatch(getDataBaki());
    // localStorageencryp('data_barang',[])
    localStorage.setItem('data_barang_tmp',"[]")
    // console.log(JSON.parse(localStorage.getItem('data_barang_tmp')));
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let barang_tmp = this.props.setBarangPindah.length === 0 ? JSON.parse(localStorage.getItem('data_barang_tmp')) : this.props.setBarangPindah 
    let hasil = {
      kode_gudang: data.kode_gudang,
      kode_toko: data.kode_baki,
      detail_barang: barang_tmp
    }
    postData('pindahbarang/addsatuan', hasil)
    .then(() => {
      this.setState({ isLoading: false });
    }).then(() => {
      localStorage.setItem('data_barang_tmp', JSON.stringify([]));
    })
    .then(() => {
      this.props.dispatch(setBarangPindah([]));
    })
    .then(() => {
      ToastNotification("success", "Data Berhasil Disimpan");
    }).catch((error) => this.errorhandling(error))
  }
  errorhandling(error) {
    // NotifError(error.response.data)
    ToastNotification("info", /conflict/i.test(error.response?.data) ? "Terjadi Kesalahan Saat Mengirim Data" : error.response?.data);
    this.setState({ isLoading: false });
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Barang</Link>
          </li>
          <li className="breadcrumb-item active">Pindah Barang</li>
        </ol>
        <h1 className="page-header">Pindah Barang </h1>
        <Panel>
          <PanelHeader>Pindah Barang</PanelHeader>
          <br />

          {/* Pindah Barang */}
          <div className="container">
            <HeadPindahBarang
              isLoading={this.state.isLoading}
              onSubmit={(data) => this.handleSubmit(data)}
              data={this.props.setBarangPindah}
              columns={this.state.columns}
            />
          </div>
          <br />
          {/* End Pindah Barang */}

          {/* End Tambah Pindah Barang  */}
        </Panel>
      </div>
    );
  }
}

export default connect(maptostate, null)(FormPindahBarang);
